
* {
  margin: 0;
  padding: 0;
}
html {
  -webkit-text-size-adjust: 100%;
}
body {
  background-color: white;
}
#app {
  font-family: "Helvetica", "Helvetica Neue";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #454545;
}
.bordered-box {
  border: 1px solid #E6E6E6;
  background-color: white;
  border-radius: 1px;
  margin: 0;
}
.input-box {
  width: 100%;
  height: 45px;
  background-color: #F9F9F9;
  border: 1px solid #E6E6E6;
  text-indent: 14px;
  font-size: 14px;
  margin-bottom: 10px;
}
nav {
  margin-top: 0;
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 50px;
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.16), 0 1px 4px rgba(0, 0, 0, 0.23);
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.16), 0 1px 4px rgba(0, 0, 0, 0.23);
  z-index: 2;
  width: 100%;
  background-color: white;
}
nav .logo-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 0;
    width: 100%;
    position: absolute;
    z-index: 0;
}
nav .logo-container #logo {
      margin: 0;
}
nav .logo-container #logo img {
        width: 120px;
        margin: 14px 0 0 0;
        opacity: 0.8;
}
nav .logo-container #logo img:hover {
        opacity: 1.0;
}
.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  background-color: white;
  overflow-x: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  padding-top: 60px;
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.16), 0 1px 4px rgba(0, 0, 0, 0.23);
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.16), 0 1px 4px rgba(0, 0, 0, 0.23);
}
.sidenav a {
  padding: 17px 8px 8px 20px;
  text-decoration: none;
  font-size: 14px;
  color: #454545;
  display: block;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.sidenav a:hover {
  color: #f1f1f1;
}
.sidenav .closebtn {
  position: absolute;
  top: 0;
  font-size: 15px;
  width: 100%;
  color: lightgrey;
}
.hamburger {
  cursor: pointer;
  margin-left: 10px;
  z-index: 2;
}
.hamburger #first, .hamburger #second, .hamburger #third {
    width: 27px;
    border-radius: 2px;
    height: 4px;
    margin: 4px;
    background-color: #454545;
}
@media screen and (max-height: 450px) {
.sidenav {
    padding-top: 15px;
}
.sidenav a {
    font-size: 18px;
}
}



